.about {
  position: relative;
  width: 100%;
  display: flex;
}

.about .contentBx {
  background: #162c3b;
  color: #fff;
  padding: 100px;
  width: 50%;
}

.heading {
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 10px;
}

.text {
  color: #fff;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
}

.about .imgBx {
  background: url(../images/anam.jpg);
  width: 50%;
  background-size: cover;
  background-position: center;
}

@media (max-width: 991px) {
  .about {
    flex-direction: column;
  }

  .about .contentBx,
  .about .imgBx {
    width: 100%;
    padding: 50px;
    min-height: 600px;
    text-align: center;
  }
}
