.heading {
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 25px;
  margin-top: 25px;
}

.text {
  color: #fff;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
}

.skills {
  background: #162c3b;
  width: 100%;
  padding: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.skills .contentBx {
  max-width: 800px;
}

.skills .imgBx {
  max-width: 250px;
}

.skills .imgBx img {
  max-width: 100%;
}

@media (max-width: 991px){
  .skills {
    padding: 50px;
    padding-top: 300px;
    justify-content: center;
    flex-direction: column;
  }

  .skills .imgBx{
      margin-top: 40px;
      max-width: 250px;
  }

}