.banner {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #272727;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.banner img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.25;
}

.banner .content {
  position: relative;
  max-width: 750px;
  text-align: center;
}

.banner .content h2 {
  font-size: 60px;
}

.banner .content p {
  font-size: 18px;
}

.banner header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 100px;
  justify-content: space-between;
  align-items: center;
  z-index: 10000;
}

.banner header .logo {
  color: #fff;
  text-decoration: none;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
}

.banner header .toggle {
  position: relative;
  width: 36px;
  height: 28px;
  cursor: pointer;
}

@media (max-width: 991px) {
  /*banner*/
  .banner {
    padding: 50px;
  }
  .banner header {
    padding: 20px 50px;
  }

  .banner .content h2 {
    font-size: 36px;
  }

  .banner .content p {
    font-size: 16px;
  }
}