.contact {
  padding: 100px;
  background:#12222d;
  text-align: center;
}


.imgBx2 {
  background: url('../images/table-mountain.jpg');
  background-size: cover;
  background-position: center;
  width: 50%;
}

.form .inputBx input {
  width: 100%;
  border: none;
  background: transparent;
  box-shadow: none;
  outline: none;
  padding: 10px 0;
  font-size: 18px;
  font-weight: 300;
  color: #fff;
  border-bottom: 2px solid #fff;
}

.form .inputBx {
  margin-top: 40px;
}

.form .inputBx input::placeholder {
  color: #fff;
}

.form .inputBx textarea {
  width: 100%;
  border: none;
  box-shadow: none;
  background: transparent;
  outline: none;
  padding: 10px 0;
  font-size: 18px;
  font-weight: 300;
  min-height: 150px;
  color: #fff;
  border-bottom: 2px solid #fff;
}

.form .inputBx textarea::placeholder {
  color: #fff;
}

.form .inputBx input[type='submit'] {
  width: 150px;
  background: #fff;
  color: #111;
  cursor: pointer;
  font-weight: 400;
}

@media (max-width: 991px){
   .contact{
      padding: 50px;
  } 
}
